@mixin font-family-serif {
	font-family: Iowan Old Style, Georgia, Cambria, Times New Roman, Droid Serif, Times, Source Serif Pro, ui-serif, serif;
}

@mixin font-family-sans {
	font-family: ui-sans-serif, -apple-system, BlinkMacSystemFont, IBM Plex Sans, Avenir Next, Avenir, Segoe UI, Helvetica Neue, Helvetica, Liberation Sans, Cantarell, Ubuntu, Roboto, Noto, Arial, sans-serif;
}

@mixin font-family-sans-rounded {
	font-family: ui-rounded, ui-sans-serif, -apple-system, BlinkMacSystemFont, Verdana, Trebuchet MS, sans-serif;
}

@mixin font-family-mono {
	font-family: ui-monospace, IBM Plex Mono, Cascadia Mono, Segoe UI Mono, Liberation Mono, Fira Code, Fira Mono, Menlo, Monaco, Consolas, monospace;
}

@mixin font-family-emoji {
	font-family: Twemoji Mozilla, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji, EmojiOne Color, Android Emoji;
}

@mixin border-radius {
	border-radius: 0.5rem;
}

@mixin transitions-standard {
	transition: background-color 0.25s, color 0.5s, letter-spacing 0s;
}

@mixin body-bg-light {
	background-image: linear-gradient(to bottom, var(--color-body-light), var(--color-white) 50%);
}

@mixin body-bg-dark {
	background-image: linear-gradient(to bottom, var(--color-body-dark), var(--color-black-dim) 50%);
}

$breakpoints: (
	"22": 22rem,
	"23": 23rem,
	"24": 24rem,
	"25": 25rem,
	"26": 26rem,
	"27": 27rem,
	"28": 28rem,
	"29": 29rem,
	"30": 30rem,
	"31": 31rem,
	"32": 32rem,
	"33": 33rem,
	"34": 34rem,
	"35": 35rem,
	"36": 36rem,
	"37": 37rem,
	"38": 38rem,
	"39": 39rem,
	"40": 40rem,
	"41": 41rem,
	"42": 42rem,
	"43": 43rem,
	"44": 44rem,
	"45": 45rem,
	"46": 46rem,
	"47": 47rem,
	"48": 48rem,
	"49": 49rem,
	"50": 50rem,
	"51": 51rem,
	"52": 52rem,
	"53": 53rem,
	"54": 54rem,
	"55": 55rem,
	"56": 56rem,
	"57": 57rem,
	"58": 58rem,
	"59": 59rem,
	"60": 60rem,
	"61": 61.65rem
);

@mixin mq($width, $type: min) {
	@if map_has_key($breakpoints, $width) {
		$width: map_get($breakpoints, $width);

		@if $type = max {
			$width: $width - 0.1rem;
		}

		@media only screen and (#{$type}-width: $width) {
			@content;
		}
	}
}

body {
	font-size: 16px;
	font-weight: 400;
	line-height: 1.65;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
table {
	font-size: 1rem;
	margin: 1rem 0 0 0;
}

cite {
	display: block;
	font-size: 65%;
	font-weight: 400;
	text-transform: uppercase;
}

h1 {
	font-size: 200%;
	font-weight: 800;
}

h2 {
	font-size: 160%;
	font-weight: 700;
	line-height: 1.35;
}

h3 {
	font-size: 120%;
	font-weight: 600;
}

h4 {
	font-size: 110%;
	font-weight: 600;
}

h5 {
	font-size: 105%;
	font-weight: 500;
}

h6 {
	font-size: 100%;
	font-weight: 500;
}

p {
	font-size: 100%;
	font-weight: 400;
}

hr {
	margin: 1.25rem 0 0 0;
	padding: 0;
}

ul {
	margin: 1rem 0 0 1.25rem;
	padding: 0;
}

li {
	margin-top: 0.25rem;

	&:first-child {
		margin-top: 0;
	}
}

em,
i {
	font-style: oblique;
}

strong,
b {
	font-weight: 600;
}

img {
	height: auto;
	max-width: 100%;
}

.center {
	text-align: center;
}

.mlra,
.twitter-tweet {
	margin-left: auto;
	margin-right: auto;
}

.m0 {
	margin-top: 0 !important;
}

.mp5 {
	margin-top: 0.5rem !important;
}

.m1 {
	margin-top: 1rem !important;
}

.m1p5 {
	margin-top: 1.5rem !important;
}

.m2 {
	margin-top: 2rem !important;
}

.m3 {
	margin-top: 3rem !important;
}

.fi {
	font-size: inherit !important;
}

.f2 {
	font-size: 200% !important;
}

.fillImg {
	img {
		@include border-radius();
		height: auto;
		width: 100%;
	}
}

.emoji {
	@include font-family-emoji;
	font-size: clamp(0.9rem, 90%, 4rem);
	line-height: 0.9rem;
}

.embedtool {
	aspect-ratio: 16 / 9;
	height: 0;
	padding-top: 56%;
	position: relative;
	overflow: hidden;
	max-width: 100%;
}

.embedtool iframe,
.embedtool object,
.embedtool embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.embedtool .fluid-vids {
	position: initial !important;
}

.flexNoWrap {
	display: flex;
	flex-flow: row wrap;
	width: 100%;

	@include mq("44") {
		flex-flow: row nowrap;
	}

	.flexItem {
		margin: 1rem 0 0 0;
		width: 100%;

		&:not(:nth-of-type(1)) {
			@include mq("44") {
				margin: 1rem 0 0 1rem;
			}
		}
	}
}

.serif {
	@include font-family-serif;
}